// /lib/localAuth.ts

export type User = { id: number, username: string, email: string, password: string };
export type Pedido = { id: number, userId: number, productName: string, quantity: number, total: number };

// ------ USUARIOS ------
export function getUsers(): User[] {
  return JSON.parse(localStorage.getItem("kwikemart_users") || "[]");
}
export function saveUser(user: User) {
  const users = getUsers();
  users.push(user);
  localStorage.setItem("users", JSON.stringify(users));
}
export function findUser(identifier: string, password: string): User | null {
  const users = getUsers();
  return users.find(u =>
    (u.username === identifier || u.email === identifier) && u.password === password
  ) || null;
}

// ------ PEDIDOS ------
export function getOrders(): Pedido[] {
  return JSON.parse(localStorage.getItem("kwikemart_orders") || "[]");
}
export function saveOrder(order: Pedido) {
  const orders = getOrders();
  orders.push(order);
  localStorage.setItem("orders", JSON.stringify(orders));
}
